<template>
	<div id="newHost" class=" w-5/6 mx-auto bg-white  rounded-xl  flex flex-col">
			<div id="map" class="w-full h-1/2 "></div>
			<div id="info" class="w-5/6 ml-6 grid grid-cols-1 justify-around mt-4 p-4">
				<div>
					<label for="lat" class=" font-semibold  w-16 inline-block">Lat</label>
					<input  name="" id="lat" class=" ml-2outline-none text-center text-pink font-semibold" readonly>
				</div>
				<div>
					<label for="lng" class=" font-semibold py-2  w-16 inline-block">Lng</label>
					<input  name="" id="lng" class=" ml-2 outline-none text-center text-pink font-semibold" readonly>
				</div>
				<div>
					<label for="location" class=" font-semibold py-2  w-16 inline-block">Country</label>
					<input  name="" id="location" class=" ml-2 text-center outline-none text-pink font-semibold" readonly>
				</div>
				<div>
					<label for="State" class=" font-semibold py-2  w-16 inline-block">State</label>
					<input  name="" id="State" class=" ml-2 text-center outline-none text-pink font-semibold" readonly>
				</div>
				<div>
					<label for="Postcode" class=" font-semibold  w-16 inline-block">Postcode</label>
					<input  name="" id="Postcode" class=" ml-2 text-center outline-none text-pink font-semibold" readonly>
				</div>


			</div>
			<div class="mx-auto p-4">
				<button class=" py-2 float-bottom px-6 text-blue border-2 border-blue hover:text-green hover:bg-blue duration-300 transition " @click="close">Add</button>
			</div>
	</div>
</template>

<script>

import L from 'leaflet'
	export default {
		name:'addHost',
		data() {
			return {
				point:{lat: 38.995583773578616, lng: 3.2623649993881014},
				location:'',
			}
		},
		mounted() {
   			var map = L.map('map',{
      			center:[38.995583773578616, 3.2623649993881014],
      			zoom:4
    		})

    		L.tileLayer('https://api.maptiler.com/maps/streets-v2/{z}/{x}/{y}.png?key=PD8XJ66HVcmOugKVcgUz',{
    		attribution: '<a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> <a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>'
			}).addTo(map);
			var markerGroup = L.layerGroup().addTo(map);
    		L.marker(this.point).addTo(markerGroup)
    		.bindPopup('<div class="text-green font-semibold w-full">Rigth Click to add marker</div><div class="text-red font-semibold w-full">Left Click to clear the map</div>')
    		.openPopup()
    		map.on("contextmenu", function(event) {
  				  var latlng = map.mouseEventToLatLng(event.originalEvent);
  					document.getElementById("lat").value = latlng.lat
  					document.getElementById("lng").value = latlng.lng
					fetch(`https://nominatim.openstreetmap.org/reverse?lat=${latlng.lat}&lon=${latlng.lng}&format=json`)
					    .then(response => response.json())
					    .then(j => {
					    	console.log(j)
					      document.getElementById("location").value = j.address.country
					      document.getElementById("Postcode").value = j.address.postcode || "Unknown"
					      document.getElementById("State").value = j.address.state || j.address.village || j.address.town
					})
  					L.marker(latlng,{draggable:true}).addTo(markerGroup)
  			})
  			map.on("click",function() {
  				markerGroup.clearLayers()
  				document.getElementById("lat").value = ''
  				document.getElementById("lng").value = ''
  				document.getElementById("location").value = ''
				document.getElementById("Postcode").value = ''
				document.getElementById("State").value =''
  				
  			})
    	}
    }

</script>
<style>
#newHost {
	height:95vh;
	box-shadow: rgb(38, 57, 77) 20px 25px 30px -10px,rgb(38, 57, 77) -20px 25px 30px -10px;
	
}

</style>	
