<template>
	<div class="grid grid-cols-1 md:flex md:flex-row justify-around">
		<div class="basis-1/4 border-2 border-gray mx-2 h-auto">
			<input type="checkbox" name="">
			<input type="checkbox" name="">
			<input type="checkbox" name="">
			<input type="checkbox" name="">
			<input type="checkbox" name="">
		</div>
		<div class="basis-3/4 mx-2 grid grid-cols-2 md:grid md:grid-cols-4 justify-around">
			<router-link  v-for="card in cards" :key="card.id" :to="{name:'Details',params:{id:card.id}}">
				<Card class="cursor-pointer" >
					<template #image>
						<img :src="card.image">
					</template>

					<template #name>
						{{card.name}}
					</template>

					<template #country-city>
						{{card.country}}, {{card.city}}
					</template>
					<template #price>
						{{card.price}}
					</template>
				</Card>		
				</router-link>
		</div>
		<!-- <Hostpopup v-show="isOpen" :host="host" /> -->
	</div>
	
</template>
<script>
/*			<router-link  v-for="card in cards" :key="card.id" :to="{name:'Details',params:{id:card.id}}">
			@click="showDetails(card.id)
*/
import Hostpopup from './../components/Hostpopup.vue'
import axios from 'axios'
import Card from './../components/Card.vue'
import { isProxy, toRaw } from 'vue';
	export default {
		name:'Search',
		created(){
			this.loadHouses()
		},
		components:{Card,Hostpopup},
		data() {
			return {
				cards:{},
				host:{},
				isOpen:false,

			}
		},
		methods: {
			loadHouses() {
				axios
					.get("houses")
					.then( response => {
						this.cards = response.data
					} )
					.catch(err =>{console.log(err)})
			},
			showDetails(id) {
				axios.get(`houses/${id}`)
					.then((response)=>{
						this.host = response.data
						this.isOpen = true
					})
					.catch((err)=>{console.log(err)})
			}
		}
	}

</script>