<template>
    <div class="mt-12 mx-auto w-full border-b-2 border-gray">
        <button id="requests" @click=" activeTab ='Requests',setActive($event)" class=" transition duration-500 rounded-tl-md py-2 px-6 border-2 border-b-0 border-gray  text-green bg-blue font-semibold">users requets</button>
        <button id="active"   @click=" activeTab =  'Active',setActive($event)" class=" transition duration-500    py-2 px-6 border-2 border-b-0 border-l-0 border-gray font-semibold">active users</button>
        <button id="blocked"  @click=" activeTab = 'Blocked',setActive($event)" class=" transition duration-500  rounded-tr-md  py-2 px-6 border-2 border-b-0 border-l-0 border-gray font-semibold">blocked users</button>
    </div>
    <div class="w-full mt-4">
        <component :is="activeTab"/>
    </div>
</template>
<script>
    import Requests from './requests.vue'    
    import Active from './active.vue'
    import Blocked from './blocked.vue'
    export default {
        name:'Users',
        components:{Requests,Active,Blocked},
        data() {
            return {
                activeTab:'Requests',
            }
        },
        methods:{
         setActive(event)  {
            const id = event.target.id
            if(id == "requests"){
                document.getElementById(id).classList.add("text-green","bg-blue")
                document.getElementById("active").classList.remove("text-green","bg-blue")
                document.getElementById("blocked").classList.remove("text-green","bg-blue")
            }
            else if(id == "active"){
                document.getElementById(id).classList.add("text-green","bg-blue")
                document.getElementById("requests").classList.remove("text-green","bg-blue")
                document.getElementById("blocked").classList.remove("text-green","bg-blue")
            }
            else{
                document.getElementById(id).classList.add("text-green","bg-blue")
                document.getElementById("requests").classList.remove("text-green","bg-blue")
                document.getElementById("active").classList.remove("text-green","bg-blue")
            }
         }
        },
    }
</script>