<template>
	<div class="w-full h-16 bg-darkblue"></div>
	<div class="flex flex-row">
		<div class="basis-1/5 bg-darkblue h-auto flex flex-col">
			<div class="w-24 h-24 rounded-full bg-white mx-auto"></div>
			<div class="text-center text-green text-xl "><button></button></div>
		</div>
		<div class="basis-3/4">
			<div class="flex flex-row w-2/3 mx-auto">
				<div id="users" class="text-2xl font-semibold cursor-pointer border-b-2 border-solid p-2 border-green text-green transition duration-500  mx-auto w-full text-center" @click="activeTab = 'Users',setActive($event)">users</div>
				<div id="hosts" class="text-2xl font-semibold cursor-pointer border-b-2 border-solid p-2  mx-auto w-full text-center transition duration-500" @click="activeTab = 'Hostings',setActive($event)">hostings</div>
			</div>
			<div class="w-4/5 h-screen mx-auto mt-4">
				<component :is="activeTab"/>
			</div>
		</div>
	</div>

</template>
<script>
import Users from './../../components/users/Users.vue'
import Hostings from './../../components/hostings/Hostings.vue'
	export default {
		name:'Admin',
		components:{Users,Hostings},
		data() {
			return {
				activeTab:"Users",
			}
		},
		methods:{
			setActive(event) {
				const id = event.target.id
				if(id == "users") {
					document.getElementById(id).classList.toggle("text-green","border-green")
					document.getElementById("hosts").classList.remove("text-green","border-green")
				}else if(id == "hosts") {
					document.getElementById("users").classList.remove("text-green","border-green")
					document.getElementById(id).classList.add("text-green","border-green")
				}
			}
		}
	}
</script>