<template>

 	<div id="map" class="mx-auto border-2 border-black shadow- w-5/6 py-64 rounded m-4"></div>
</template>

<script>
import L from 'leaflet'
import axios from 'axios'
export default {
   name: 'Map',
   props:[],
   mounted() {
   	var map = L.map('map',{
      center:[38.995583773578616,3.2623649993881014],
      zoom:6
    })
    L.tileLayer('https://api.maptiler.com/maps/streets-v2/{z}/{x}/{y}.png?key=PD8XJ66HVcmOugKVcgUz', {
    attribution: '<a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> <a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>'
	}).addTo(map)
	//add mark to the map
	L.marker([38.995583773578616,3.2623649993881014]).addTo(map)
    .bindPopup(`<b>Location</b>`)
    .openPopup()

    this.getHostings()
    },
    methods:{
        getHostings() {
            axios.get("map").then((response)=>{console.log(response.data)})
        }
    }


}

</script>