<template>
	<div class="flex flex-col w-5/6 rounded bg-white shadow-lg shadow-blue mx-auto">
		<div class="w-3/4 mx-auto my-12"><Carousel :images="data.details" /></div>
		<div class="flex flex-row">
			<div class="text-gray text-center flex flex-col mx-auto">
				<div class="text-gray font-semibold mx-auto">NAME:<span class="text-green"></span> {{data.name}}</div>	
				<div class="text-gray font-semibold mx-auto">PRICE:<span class="text-green"></span> {{data.price}}</div>	
				<div class="text-gray font-semibold mx-auto">LOCATION<span class="text-green"></span> {{data.location}}</div>	
				<div class="text-gray font-semibold mx-auto">COUNTRY<span class="text-green"></span> {{data.country}}</div>		
			</div>
			<Map v-if="data.lat&&data.lat" :lat="data.lat" :lng="data.lng"/>
		</div>
	</div>
</template>
<script>
import axios from 'axios'
import Map from '../components/Map.vue'
import Carousel from './../components/Carousel.vue'
	export default {
		name:'Details',
		components:{Map,Carousel},
		data() {
			return {

			data:{}
			}

		},
		created() {
			this.loadDetails(this.$route.params.id)
		},
		methods:{
			async loadDetails(id) {
				await axios.get(`houses/${id}`)
				.then((response)=>{
					this.data = response.data
				})
				.catch(err =>{console.log(err)})
			}
		}
	}
</script>