<template>
	<div class="w-1/2 mx-auto flex flex-row">
		<div id="info" class="sm:text-2xl text-xl font-semibold p-1 border-b-2  w-full text-center border-green text-green transition duration-500" @click="activeTab = 'Informations',setActive($event)">Informations</div>
		<div id="hosts" class="sm:text-2xl text-xl font-semibold p-1 border-b-2  w-full text-center transition duration-500" @click="activeTab = 'MyHostings',setActive($event)">Hostings</div>
	</div>
	<div class="w-5/6 mx-auto">
		<component :is="activeTab"/>
	</div>
</template>
<script>
import Informations from './../components/Informations.vue'
import MyHostings from './../components/MyHostings.vue'
	export default {
		components:{Informations,MyHostings},
		data() {
			return {
				activeTab:"Informations",
			}
		},
		methods:{
			setActive(event) {
				const id = event.target.id
				if(id == "info") {
					document.getElementById(id).classList.toggle("text-green","border-green")
					document.getElementById("hosts").classList.remove("text-green","border-green")
				}else if(id == "hosts") {
					document.getElementById("info").classList.remove("text-green","border-green")
					document.getElementById(id).classList.add("text-green","border-green")
				}
			}
		}

	}
</script>