<template>

	<div class="w-full ">
		<div>
		<div class="mt-12 mx-auto w-full ">
        	<button id="activeHosts" @click=" activeTab ='activeHosts',setActive($event)" class=" transition duration-500 rounded-tl-md py-2 sm:px-6 px-2 border-2 border-b-0 border-black  text-green bg-black font-semibold">active hosts</button>

        	<button id="availableHosts"   @click=" activeTab =  'availableHosts',setActive($event)" class=" transition duration-500 border-l-0   py-2 sm:px-6 px-2 border-2 border-b-0  border-black font-semibold">available hosts</button>
        	<button id="newHosts" @click=" activeTab ='newHosts',setActive($event)" class=" transition duration-500  py-2 sm:px-6 px-2 border-2 border-b-0 border-l-0 border-black font-semibold rounded-tr-md ">new hosts</button>
        	
   		 </div>
			<div class="w-full h-full mx-auto bg-white  border-2 rounded-tr-lg rounded-br-lg rounded-bl-lg border-black">
				<component :is="activeTab"/>
			</div>
		</div>
	</div>
	

	
		

</template>
<script>
import activeHosts from './userHostings/activeHosts.vue'
import availableHosts from './userHostings/availableHosts.vue'
import newHosts from './userHostings/newHosts.vue'
	export default {
		name:'MyHosting',
		components:{activeHosts,availableHosts,newHosts},
		data() {
			return {
				activeTab:'activeHosts'
			}
		},
		methods:{
			setActive(event) {
				const id = event.target.id
				if(id == "activeHosts") {
					document.getElementById(id).classList.add("text-green","bg-black")
					document.getElementById("availableHosts").classList.remove("text-green","bg-black")
					document.getElementById("newHosts").classList.remove("text-green","bg-black")
				}else if(id == "availableHosts") {
					document.getElementById(id).classList.add("text-green","bg-black")
					document.getElementById("activeHosts").classList.remove("text-green","bg-black")
					document.getElementById("newHosts").classList.remove("text-green","bg-black")
				}else if (id == "newHosts") {
					document.getElementById(id).classList.add("text-green","bg-black")
					document.getElementById("availableHosts").classList.remove("text-green","bg-black")
					document.getElementById("activeHosts").classList.remove("text-green","bg-black")
				}
			}
		}
	}
</script>