<template>
	    <div class="mt-2">
            <table class="table-fixed w-full">
              <thead>
                <tr class="text-blue bg-gray2">
                  <th class="p-2" v-for="head in header" :key="head"><div>{{head}}</div></th>
                </tr>
              </thead>
              <tbody>
                <tr :if="data" class="text-center border-b transition duration-500 border-green" v-for="db in data" :key="db.id">
                  <td class="px-2 font-medium" v-for="info in information" :key="info">{{db[info]}}</td>
                  <td class="px-2 font-medium">
                    <button @click=" showDetails(db)" class="px-6 py-1 border-0 hover:bg-blue transition duration-300 rounded-lg hover:text-green">Show</button>
                  </td>
                </tr>
              </tbody>
            </table>
            <Details :user="details" v-show="isOpen"/>
        </div>
	
</template>
<script>
import axios from 'axios'
import Details from './users/userDetails.vue'
export default {
		components:{Details},	
		props:["header","link","status","information"],
		data() {
			return {
				isOpen:false,
				data:null,
				details:{},
			}
		},
		created() {
			this.load(this.link)
		},
		methods: {
			load(url) {
				axios.get(url,{
					params:{
						status:this.status
					}
				})
					.then((response)=>{this.data = response.data})
					.catch((err)=>{console.log(err)})
			},
			showDetails(data) {
            	this.details = data
            	this.isOpen = true
          	}
		}







	}
</script>